.vz-page__header {
  text-transform: uppercase;
  letter-spacing: .5rem;
  font-size: 87.5%;
  margin-bottom: 3rem;
  font-style: italic;
  font-size: 2.5rem;
  font-weight: 500; }

html {
  font: 16px/1.618 "Titillium Web", "sans-serif";
  background-color: #FFF;
  box-sizing: border-box;
  height: 100%; }

* {
  box-sizing: inherit; }

body {
  margin: 0;
  height: 100%; }

.vz-action-card {
  border: 1px solid rgba(0, 0, 0, 0.06);
  background-color: #fff;
  padding: 2rem 2rem;
  margin: 0 auto 2rem;
  max-width: 50rem;
  width: 100%; }

.vz-action-card__title {
  text-transform: uppercase;
  letter-spacing: .5rem;
  font-size: 87.5%;
  margin-bottom: 1rem;
  font-style: italic;
  font-size: 1.5rem;
  font-weight: 500; }

.vz-action-card__description {
  margin-bottom: 2.5rem; }

.vz-brand {
  background-color: #000;
  height: 100%; }
  .vz-brand a {
    height: 100%;
    display: flex; }
  .vz-brand img {
    color: inherit;
    text-decoration: inherit;
    border: none;
    height: 1.5rem;
    margin: auto;
    display: block; }

.vz-breadcrumbs {
  text-transform: uppercase;
  letter-spacing: .5rem;
  font-size: 87.5%; }

.vz-breadcrumbs__item {
  display: inline-block; }
  .vz-breadcrumbs__item a {
    color: inherit;
    text-decoration: inherit;
    border: none; }
    .vz-breadcrumbs__item a:focus, .vz-breadcrumbs__item a:hover {
      background: CORAL; }
  .vz-breadcrumbs__item:not(:last-child):after {
    content: "/";
    padding: 0 1rem;
    opacity: .5; }

.vz-button, .vz-link {
  transition: all 0.2s ease-in-out;
  border: 1px solid transparent;
  text-transform: capitalize;
  color: rgba(0, 0, 0, 0.6);
  display: inline-block;
  padding: .5rem 3rem;
  white-space: nowrap;
  line-height: 1.5rem;
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
  cursor: pointer; }

.vz-button--small {
  padding: .3rem 1.5rem; }

.vz-button--micro {
  font-size: .9rem;
  padding: 0; }

.vz-button--square {
  padding: .5rem .8rem; }

.vz-button--close {
  background-color: rgba(0, 0, 0, 0.6);
  color: #FFF; }

.vz-button--close:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.vz-button--full-width {
  margin-bottom: .5rem;
  display: block;
  width: 100%; }

.vz-button--primary {
  background-color: #007bff;
  color: #FFF; }

.vz-button--primary:hover {
  background-color: #0054af; }

.vz-button--secondary {
  background-color: rgba(0, 0, 0, 0.6);
  color: #FFF; }

.vz-button--secondary:hover {
  background-color: rgba(0, 0, 0, 0.7); }

.vz-button--no {
  background-color: CORAL;
  color: #FFF; }

.vz-button--no:hover {
  background-color: #d66d47; }

.vz-button--yes {
  background-color: #07be8c;
  color: #FFF; }

.vz-button--yes:hover {
  background-color: #079c73; }

.vz-button--danger {
  background-color: CRIMSON;
  color: #FFF; }

.vz-button--danger:hover {
  background-color: #c31334; }

.vz-link {
  background-color: transparent; }

.vz-link--primary {
  color: #007bff; }

.vz-link--secondary {
  color: rgba(0, 0, 0, 0.6); }

.vz-link--yes {
  color: #07be8c; }

.vz-link--no {
  color: CORAL; }

.vz-link--danger {
  color: CRIMSON; }

.vz-comment {
  background-color: #FFF;
  margin: 1rem 1rem 2rem;
  position: relative; }

.vz-comment--tail-left::before, .vz-comment--tail-right::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: .5rem solid transparent; }

.vz-comment--tail-left::before {
  right: 100%;
  bottom: 1rem;
  border-bottom-color: #FFF;
  border-right-color: #FFF;
  transition: border-color 250ms; }

.vz-comment--tail-right::before {
  left: 100%;
  bottom: 1rem;
  border-bottom-color: #FFF;
  border-left-color: #FFF;
  transition: border-color 250ms; }

.vz-comment__body {
  transition: border-color 250ms;
  padding: 1rem; }

.vz-comment__actions {
  position: absolute;
  font-size: 90%;
  padding: 0;
  top: 100%;
  right: 0; }

.vz-comment--is-active, .vz-comment--is-active-right, .vz-comment--is-active-left {
  background-color: GAINSBORO; }

.vz-comment--is-active-right::before {
  border-bottom-color: GAINSBORO;
  border-left-color: GAINSBORO; }

.vz-comment--is-active-left::before {
  border-bottom-color: GAINSBORO;
  border-right-color: GAINSBORO; }

.vz-container {
  margin: 0 auto;
  max-width: 75rem; }

.vz-container--narrow {
  max-width: 30rem; }

.vz-container--fluid {
  max-width: 100%; }

.vz-count-bubble {
  background-color: #000;
  position: absolute;
  padding: 0 .5rem;
  color: #FFF;
  opacity: .8; }

.vz-count-bubble--faded {
  opacity: .6; }

.vz-flash-container {
  margin-bottom: 1rem;
  display: flex; }

.vz-flash-container__indicator {
  background-color: rgba(0, 0, 0, 0.6);
  margin-right: 5px;
  height: 30px;
  width: 10px; }

.vz-flash-use-icon {
  display: flex;
  width: 30px; }

.vz-flash-use-icon__img {
  max-width: 75%;
  margin: 0 auto;
  height: auto;
  width: 100%; }

.vz-flash-container__flash {
  border: 1px solid rgba(0, 0, 0, 0.05);
  background-color: #FFF;
  align-items: center;
  padding-left: 1rem;
  display: flex;
  height: 30px;
  width: 100%; }
  .vz-flash-container__flash p {
    margin: 0; }

.vz-flash-container__indicator--ok {
  background-color: #07be8c; }

.vz-flash-container__indicator--wrong {
  background-color: CORAL; }

.vz-flash-container__indicator--warning {
  background-color: GOLD; }

.vz-flash-container__indicator--info {
  background-color: LIGHTBLUE; }

.vz-form-group {
  background-color: transparent;
  margin-bottom: 1rem;
  width: 100%; }

.vz-form-group--invalid input {
  border: 1px solid CORAL; }

.vz-form-group--invalid p {
  color: CORAL;
  margin: 0; }

.vz-form-group--valid input {
  border: 1px solid #07be8c; }

.vz-form-group--valid p {
  margin: 0;
  color: #07be8c; }

.vz-form-group__control {
  background-color: transparent;
  display: block; }

.vz-form-group__label {
  margin-bottom: .25rem; }

.vz-form-group__feedback {
  font-size: 87.5%; }

.vz-form-group__help {
  color: rgba(0, 0, 0, 0.6) !important; }

.vz-form-check input {
  margin: .5rem .5rem .5rem 0;
  font-size: 100%; }

.vz-form-check--radio input {
  margin: .5rem .5rem .5rem 0;
  font-size: 100%; }

.vz-form-input {
  border: 1px solid rgba(0, 0, 0, 0.08);
  font-size: 1rem;
  padding: .75rem;
  width: 100%; }

input::placeholder {
  font-size: 1rem;
  color: #000; }

input:focus {
  background-color: #FFF;
  border-color: GAINSBORO;
  outline: 0;
  box-shadow: 0 0 0 0.15rem rgba(123, 123, 123, 0.25); }

.vz-form-input--select {
  border: 1px solid rgba(0, 0, 0, 0.08);
  background-color: #FFF;
  font-size: 1rem;
  height: 44px;
  width: 100%; }

.vz-form-input--select:focus {
  box-shadow: 0 0 0 0.15rem rgba(123, 123, 123, 0.25);
  background-color: #FFF;
  border-color: GAINSBORO;
  outline: 0; }

.vz-jumbotron {
  border: 1px solid rgba(0, 0, 0, 0.06);
  background-color: #FFF;
  padding: 2.5rem;
  width: 100%; }

.vz-jumbotron__title {
  text-transform: uppercase;
  letter-spacing: .5rem;
  font-size: 87.5%;
  margin-bottom: 3rem;
  font-style: italic;
  font-weight: 500;
  font-size: 3rem; }

.vz-jumbotron__description {
  margin-bottom: 3.5rem;
  font-weight: 500;
  font-size: 1.5rem; }

.vz-table {
  border-collapse: collapse;
  margin: 0 auto 2rem auto;
  text-align: left;
  width: 100%; }
  .vz-table thead {
    font-size: 87.5%; }
  .vz-table tbody tr:nth-child(even) {
    transition: all 0.15s ease-out;
    background-color: rgba(0, 0, 0, 0.05); }
  .vz-table tbody tr:nth-child(odd) {
    transition: all 0.2s ease-out; }
  .vz-table tbody tr:hover {
    background-color: rgba(0, 0, 0, 0.1); }
    .vz-table tbody tr:hover .vz-table__actions a {
      opacity: 1; }
  .vz-table td, .vz-table th {
    padding: .75rem 1rem; }
  .vz-table th[scope=row] {
    text-align: start; }
  .vz-table th[scope=col] {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
    background-color: #f5f5f5;
    padding-bottom: 1rem;
    position: sticky;
    top: 0; }
  .vz-table .vz-table__actions {
    white-space: nowrap; }
  .vz-table .vz-table__actions a {
    transition: all 0.15s ease-out;
    margin-right: .75rem;
    color: #007bff;
    opacity: 0; }
  .vz-table .vz-table__actions a:hover {
    color: #0054af; }

.vz-table--compact td, .vz-table--compact th {
  padding: .25rem .5rem; }

.vz-layout {
  height: 100%;
  display: grid;
  grid-template-columns: 18rem 1fr;
  grid-template-rows: 64px 1fr;
  grid-template-areas: "brand breadcrumbs" "menu content"; }

.vz-layout__brand {
  grid-area: brand;
  background-color: black;
  color: white; }

.vz-layout__breadcrumbs {
  grid-area: breadcrumbs;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  background: linear-gradient(90deg, #333, #777);
  color: #f5f5f5;
  padding: 1rem 2rem; }

.vz-layout__content {
  grid-area: content;
  background: whitesmoke;
  padding: 2rem; }

.vz-layout__menu {
  grid-area: menu; }

.vz-logo {
  background-color: black; }
  .vz-logo img {
    height: 1.5em; }

.vz-sidebar {
  height: 100%;
  display: flex;
  flex-flow: column nowrap; }

.vz-sidebar__options {
  flex: 1 0 auto; }

.vz-sidebar__user {
  flex: 0 0 auto; }

.vz-user::before {
  content: "";
  display: block;
  margin: 0 2rem;
  height: 1rem;
  border-bottom: 1px solid GAINSBORO; }

.vz-user__details {
  padding: 2rem;
  display: flex;
  flex-flow: row nowrap;
  align-items: center; }

.vz-user__avatar {
  margin-right: 1rem; }
  .vz-user__avatar img {
    height: 3rem;
    width: 3rem;
    border-radius: 999px; }

.vz-user__name {
  font-weight: 600; }

.vz-user__roles {
  font-size: 87.5%; }

.vz-user__actions {
  margin-top: 2rem; }
  .vz-user__actions a {
    color: inherit;
    text-decoration: inherit;
    border: none;
    display: block;
    text-align: center;
    text-transform: uppercase;
    font-size: 87.5%;
    padding: 1rem 2rem; }
    .vz-user__actions a:focus, .vz-user__actions a:hover {
      background-color: CORAL;
      color: white; }

.vz-pagination {
  margin: 2rem 0; }

.vz-pagination__content {
  display: flex;
  justify-content: center; }

.vz-pagination__button {
  color: inherit;
  text-decoration: inherit;
  border: none;
  background-color: transparent;
  transition: all 0.2s ease-in-out;
  text-transform: uppercase;
  padding: .5rem 1rem;
  margin: 0 .25rem;
  color: #000; }

.vz-pagination__button--active {
  background-color: #007bff; }

.vz-pagination__button:hover {
  background-color: rgba(0, 0, 0, 0.8); }

.vz-pagination__button--disabled {
  background-color: rgba(0, 0, 0, 0.4);
  cursor: not-allowed; }

.vz-pagination__button--disabled:hover {
  background-color: rgba(0, 0, 0, 0.4); }

.ks-section {
  margin: 5rem 0; }
